export function numberCity(viewer,tileset){
    tileset.tileVisible.addEventListener((tile) => {
        var content = tile.content
        var featuresLength = content.featuresLength
        for (var i = 0; i < featuresLength; i += 2) {
          const feature = content.getFeature(i)
          const model = feature.content._model
          if (model && model._sourcePrograms && model._rendererResources) {
            Object.keys(model._sourcePrograms).forEach((key) => {
              const program = model._sourcePrograms[key]
              const fragmentShader = model._rendererResources.sourceShaders[program.fragmentShader]
              let vPosition = ''
              if (fragmentShader.indexOf(' v_positionEC;') !== -1) {
                vPosition = 'v_positionEC'
              } else if (fragmentShader.indexOf(' v_pos;') !== -1) {
                vPosition = 'v_pos'
              }
            //   const color = `vec4(${feature.color.toString()})`
              const color = `vec4(0, 0.5, 1.0,0.1)`
              // const color = `vec4(0,150,255,0.6)`
              console.log(vPosition)
              var shaderStr =`
              varying vec3 ${vPosition};// 
              void main(void){
                  /*  */
                  vec4 v_helsing_position = czm_inverseModelView * vec4(${vPosition},1);// 
                  float stc_pl = fract(czm_frameNumber / 120.0) * 3.14159265 * 2.0;
                  float stc_sd = v_helsing_position.z / 60.0 + sin(stc_pl) * 0.1;
                  gl_FragColor = ${color};// 
                  gl_FragColor *= vec4(stc_sd, stc_sd, stc_sd, 1.0);// 
                  /*  */
                  float glowRange = 200.0; // ()360
                  float stc_a13 = fract(czm_frameNumber / 200.0);// /16
                  float stc_h = clamp(v_helsing_position.z / glowRange, 0.0, 1.0);
                  stc_a13 = abs(stc_a13 - 0.5) * 2.0;
                  float stc_diff = step(0.005, abs(stc_h - stc_a13));// 
                  gl_FragColor.rgb += gl_FragColor.rgb * (1.0 - stc_diff);// 
              }`
              // console.log(shaderStr)
              // 
              model._rendererResources.sourceShaders[program.fragmentShader] =shaderStr
              // 

            //   `
            //   varying vec3 ${vPosition};// 
            //   void main(void){
            //       /*  */
            //       vec4 v_helsing_position = czm_inverseModelView * vec4(${vPosition},1);// 
            //       float stc_pl = fract(czm_frameNumber / 120.0) * 3.14159265 * 2.0;
            //       /*   v_helsing_position.z / 5.0 + sin(stc_pl) * 0.1 5.05*/
            //       float stc_sd = v_helsing_position.z / 220.0 + sin(stc_pl) * 0.1;
            //       gl_FragColor = ${color};// 
            //       gl_FragColor *= vec4(stc_sd, stc_sd, stc_sd, 0.6);// 
            //       /*  */
            //       float glowRange = 360.0; // ()360
            //       /* 360 */
            //       //float stc_a13 = fract(czm_frameNumber / 360.0);// /6*606
            //       float stc_a13 = fract(czm_frameNumber / 200.0);// /6*606
            //       float stc_h = clamp(v_helsing_position.z / glowRange, 0.0, 1.0);
            //       stc_a13 = abs(stc_a13 - 0.5) * 2.0;
            //       float stc_diff = step(0.005, abs(stc_h - stc_a13));// 
            //       //gl_FragColor.rgb += gl_FragColor.rgb * (1.0 - stc_diff);// 
            //   }`
            })
            // 
            model._shouldRegenerateShaders = true
          }
        }
      })
}